import React from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import ParticipantView from './components/ParticipantView';
import AdminView from './components/AdminView';
import { QuizProvider } from './context/QuizContext';

function App() {
  // Définir le basename en fonction de l'environnement
  const basename = process.env.NODE_ENV === 'production' ? '/NilasQuizz' : '';

  return (
    <BrowserRouter basename={basename}>
      <Routes>
        {/* Vue Admin - pas besoin du QuizProvider */}
        <Route path="/admin" element={<AdminView />} />
        
        {/* Wrapper QuizProvider pour toutes les vues participant */}
        <Route element={<QuizProvider />}>
          <Route path="/" element={<ParticipantView />} />
          <Route path="*" element={<ParticipantView />} />
        </Route>
      </Routes>
    </BrowserRouter>
  );
}

export default App;