import { io } from 'socket.io-client';

// Configuration du socket avec reconnexion automatique
export const socket = io('https://nilasquizz.glitch.me', {
  reconnection: true,
  reconnectionAttempts: 5,
  reconnectionDelay: 1000,
  autoConnect: true
});

// Gestion améliorée des événements de base
socket.on('connect', () => {
  console.log('Connected to server, socket ID:', socket.id);
  // Demander le statut actuel après la connexion
  socket.emit('get-current-status');
});

socket.on('disconnect', () => {
  console.log('Disconnected from server');
});

socket.on('connect_error', (error) => {
  console.error('Erreur de connexion:', error);
});

// Service socket amélioré
export const socketService = {
  // Pour les participants
  submitAnswer: (answer) => {
    if (socket.connected) {
      socket.emit('submit-answer', answer);
      return true;
    }
    console.error('Socket non connecté lors de la soumission de la réponse');
    return false;
  },
  
  // Méthodes pour s'abonner aux événements avec gestion d'erreur
  onQuestionChange: (callback) => {
    socket.on('question', (question) => {
      try {
        callback(question);
      } catch (error) {
        console.error('Erreur dans le callback onQuestionChange:', error);
      }
    });
  },
  
  onStatsUpdate: (callback) => {
    socket.on('stats-update', (stats) => {
      try {
        callback(stats);
      } catch (error) {
        console.error('Erreur dans le callback onStatsUpdate:', error);
      }
    });
  },
  
  // Nettoyage amélioré
  cleanup: () => {
    ['question', 'stats-update', 'quiz-ended'].forEach(event => {
      socket.off(event);
    });
  }
};

// Export par défaut pour faciliter l'utilisation
export default socketService;