import React, { useState, useEffect } from 'react';
import { Box, Button, Typography, Container, Paper } from '@mui/material';
import { socket } from '../services/socket';
import { questions } from '../data/questions';

function AdminView() {
  const [isQuizStarted, setIsQuizStarted] = useState(false);
  // On commence à -1 pour indiquer qu'aucune question n'est encore envoyée
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(-1);
  const [stats, setStats] = useState({ total: 0, correct: 0 });

  const startQuiz = () => {
    console.log("Démarrage du quiz");
    socket.emit('admin:start-quiz');
    setIsQuizStarted(true);
    // On envoie la première question (index 0)
    handleNextQuestion();
  };

  const resetQuiz = () => {
    socket.emit('admin:reset-quiz');
    setIsQuizStarted(false);
    setCurrentQuestionIndex(-1);
  };

  const handleNextQuestion = () => {
    // On incrémente d'abord l'index
    const nextIndex = currentQuestionIndex + 1;
    console.log("Prochain index:", nextIndex);

    if (nextIndex < questions.length) {
      const nextQuestion = questions[nextIndex];
      console.log("Envoi de la question:", nextQuestion);
      socket.emit('admin:next-question', nextQuestion);
      setCurrentQuestionIndex(nextIndex);
    }
  };

  return (
    <Container maxWidth="md">
      <Box sx={{ mt: 4 }}>
        <Typography variant="h4" gutterBottom>
          Panel Administrateur
        </Typography>

        {!isQuizStarted ? (
          <Paper sx={{ p: 3, mt: 3, textAlign: 'center' }}>
            <Typography variant="h6" gutterBottom>
              En attente de démarrage du quiz
            </Typography>
            <Button 
              variant="outlined" 
              color="error" 
              onClick={resetQuiz}
              sx={{ mt: 3, ml: 2 }}
            >
              Réinitialiser le Quiz
            </Button>
            <Button
              variant="contained"
              color="primary"
              size="large"
              onClick={startQuiz}
              sx={{ mt: 2 }}
            >
              Démarrer le Quiz
            </Button>
          </Paper>
        ) : (
          <>
            <Paper sx={{ p: 3, mt: 3 }}>
              <Typography variant="h6">
                Question {currentQuestionIndex + 1}/{questions.length}
              </Typography>
              {currentQuestionIndex >= 0 && (
                <Typography sx={{ mt: 2 }}>
                  Question en cours: {questions[currentQuestionIndex].text}
                </Typography>
              )}
            </Paper>
            <Paper sx={{ p: 3, mt: 3 }}>
              <Typography variant="h6">Statistiques en direct</Typography>
              <Box sx={{ mt: 2 }}>
                <Typography>Réponses totales: {stats.total}</Typography>
                <Typography>Réponses correctes: {stats.correct}</Typography>
              </Box>
            </Paper>

            <Button
              variant="contained"
              color="primary"
              onClick={handleNextQuestion}
              sx={{ mt: 3 }}
              disabled={currentQuestionIndex >= questions.length}
            >
              {currentQuestionIndex < questions.length ? "Question suivante" : "Terminer le quiz"}
            </Button>
          </>
        )}
      </Box>
    </Container>
  );
}

export default AdminView;