import React, { useState, useEffect } from 'react';
import { Box, Button, Typography, Container, LinearProgress } from '@mui/material';
import { socket } from '../services/socket';

function ParticipantView() {
  const [isQuizStarted, setIsQuizStarted] = useState(false);
  const [currentQuestion, setCurrentQuestion] = useState(null);
  const [selectedAnswer, setSelectedAnswer] = useState(null);
  const [isAnswered, setIsAnswered] = useState(false);

  useEffect(() => {
    // Assurer que le socket est connecté
    if (!socket.connected) {
      socket.connect();
    }

    // Gestionnaire de questions avec log de débogage
    const handleQuestion = (question) => {
      console.log("Nouvelle question reçue:", question);
      setCurrentQuestion(question);
      setSelectedAnswer(null);
      setIsAnswered(false);
    };

    // Gestionnaire de statut du quiz
    const handleQuizStatus = ({ isStarted }) => {
      console.log("Status du quiz mis à jour:", isStarted);
      setIsQuizStarted(isStarted);
      if (!isStarted) {
        setCurrentQuestion(null);
        setSelectedAnswer(null);
        setIsAnswered(false);
      }
    };

    // Mise en place des écouteurs d'événements
    socket.on('question', handleQuestion);
    socket.on('quiz-status', handleQuizStatus);

    // Vérifier le statut actuel au montage
    socket.emit('get-current-status');

    // Nettoyage des écouteurs
    return () => {
      socket.off('question', handleQuestion);
      socket.off('quiz-status', handleQuizStatus);
    };
  }, []); // Dépendances vides pour n'exécuter qu'au montage

  const handleAnswerSubmit = (answer) => {
    if (!isAnswered && currentQuestion) {
      setSelectedAnswer(answer);
      setIsAnswered(true);
      socket.emit('submit-answer', {
        answer,
        isCorrect: answer === currentQuestion.correct,
        questionId: currentQuestion.id
      });
      console.log("Réponse soumise:", answer);
    }
  };
  
  const renderTextOptions = () => (
    <Box sx={{ display: 'grid', gap: 2, mt: 4 }}>
      {currentQuestion.options.map((option, index) => (
        <Button
          key={index}
          variant={selectedAnswer === option ? "contained" : "outlined"}
          onClick={() => handleAnswerSubmit(option)}
          disabled={isAnswered}
          sx={{ 
            height: '60px',
            transition: 'all 0.2s',
            '&:hover': {
              transform: !isAnswered && 'scale(1.02)'
            }
          }}
        >
          {option}
        </Button>
      ))}
    </Box>
  );

  const renderImageOptions = () => (
  <Box sx={{ 
    display: 'grid',
    gridTemplateColumns: 'repeat(2, 1fr)',
    gap: 2,
    mt: 2
  }}>
    {currentQuestion.options.map((option) => (
      <Box
        key={option.id}
        onClick={() => !isAnswered && handleAnswerSubmit(option.id)}
        sx={{
          cursor: isAnswered ? 'default' : 'pointer',
          border: selectedAnswer === option.id ? '4px solid #1976d2' : '2px solid #e0e0e0',
          borderRadius: '8px',
          overflow: 'hidden',
          transition: 'all 0.2s',
          '&:hover': {
            transform: isAnswered ? 'none' : 'scale(1.02)',
            boxShadow: isAnswered ? 'none' : '0 4px 8px rgba(0,0,0,0.1)'
          }
        }}
      >
        <img
          src={option.image}
          alt={`Option ${option.id}`}
          onError={(e) => {
            console.error("Erreur de chargement de l'image:", option.image);
            e.target.style.backgroundColor = '#f0f0f0';
            e.target.style.padding = '20px';
            e.target.style.textAlign = 'center';
          }}
          style={{
            width: '100%',
            height: 'auto',
            display: 'block',
            minHeight: '150px'
          }}
        />
      </Box>
    ))}
  </Box>
);

  // Écran d'attente initial
  if (!isQuizStarted) {
    return (
      <Container maxWidth="sm">
        <Box
          sx={{
            mt: 8,
            textAlign: 'center',
            p: 4,
            borderRadius: 2,
            bgcolor: 'background.paper',
            boxShadow: '0 4px 12px rgba(0,0,0,0.1)'
          }}
        >
          <Typography variant="h4" gutterBottom color="primary">
            Bienvenue au Quiz !
          </Typography>
          <Typography variant="h6" gutterBottom color="text.secondary">
            En attente du démarrage du quiz...
          </Typography>
          <LinearProgress sx={{ mt: 4 }} />
        </Box>
      </Container>
    );
  }

  // Écran d'attente de question
  if (!currentQuestion) {
    return (
      <Container maxWidth="sm">
        <Box sx={{ mt: 4, textAlign: 'center' }}>
          <Typography variant="h5">
            En attente de la prochaine question...
          </Typography>
          <LinearProgress sx={{ mt: 4 }} />
        </Box>
      </Container>
    );
  }

  // Affichage de la question
  return (
    <Container maxWidth="sm">
      <Box 
        sx={{ 
          mt: 4, 
          textAlign: 'center',
          p: 3,
          borderRadius: 2,
          bgcolor: 'background.paper',
          boxShadow: '0 2px 8px rgba(0,0,0,0.1)'
        }}
      >
        <Typography variant="h5" gutterBottom>
          {currentQuestion.text}
        </Typography>

        {currentQuestion.imageOptions ? renderImageOptions() : renderTextOptions()}

        {isAnswered && (
          <Typography 
            sx={{ 
              mt: 2, 
              color: 'success.main',
              p: 2,
              bgcolor: 'success.light',
              borderRadius: 1,
              color: 'white'
            }}
          >
            Réponse enregistrée !
          </Typography>
        )}
      </Box>
    </Container>
  );
}

export default ParticipantView;